    for (var i=0; i<distances.length; i++) {
      let d = distances[i];
      ds.push({
        d: d,
        p: 1 - Math.pow(1- Math.pow(collision_probability(r, d), K), L)
      });
    }
    return ds;
  })();

</script>

<style>
  .container {
    margin-left: auto;
    margin-right: auto;
    width: 50%;
  }
  .container:after {
    content: "";
    display: block;
    padding-bottom: 100%;
  }
  .content {
    position:absolute;
    width: 100%;
    height: 100%;
  }
</style>

<div>
  <div>
    <label for="volume">Quantization width <d-math>r = {r}</d-math></label>
    <input type=range bind:value={r} min=0.1 max=2 step=0.1>
  </div>
  <div>
    <label for="volume">Number of contatenations $$K$$={K}</label>
    <input type=range bind:value={K} min=1 max=5 step=1>
  </div>
  <div>
    <label for="volume">Number of contatenations $$L$$={L}</label>
    <input type=range bind:value={L} min=1 max=200 step=1>
  </div>
  <figure class="container">
    <div class="content">
      <LayerCake data={data} yDomain={[0, 1]} x="d" y ="p">
        <Svg>
          <AxisX baseline=true/>
          <AxisY baseline=true textAnchor="end" xTick=-1/>
          <Line stroke_width=2/>
        </Svg>
      </LayerCake>
    </div>
  </figure>
</div>